@keyframes move-up {
  0% {
    top: 1.5625rem;
    opacity: 1;
  }
  100% {
    top: -3.125rem;
    opacity: 0;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes appear {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes pop {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
html,
body {
  font-family: monospace;
  font-size: 1rem;
  line-height: 1.25;
  color: #776e65;
  height: 100%;
  padding: 0;
  margin: 0;
  background-color: #fff;
}

h1 {
  font-size: 2.5rem;
  line-height: 1;
  margin: 0;
}

.container {
  max-width: 38.924rem;
  padding: 1rem;
  margin: 0 auto;
}

.game {
  position: relative;
  padding: 1rem;
  box-sizing: border-box;
  background: #bbada0;
  border-radius: 0.5rem;
  cursor: default;
  user-select: none;
}

.header {
  font-size: 1.125rem;
}
.header p {
  letter-spacing: -0.01em;
  margin: 0;
}
.header__top, .header__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 1rem;
}
.header__top {
  justify-content: flex-end;
}
.header__top h1 {
  margin-right: auto;
}
.header__bottom {
  justify-content: space-between;
}

.score {
  font-size: 2rem;
  line-height: 1;
  color: #fff;
  text-align: center;
  font-weight: 700;
  padding: 0.5rem 1rem;
  position: relative;
  background: #bbada0;
  border-radius: 0.25rem;
}
.score::before {
  font-size: 0.875rem;
  line-height: 1;
  color: #eee4da;
  text-transform: uppercase;
  display: block;
  padding-bottom: 0.5rem;
}
.score-addition {
  font-size: 1.75rem;
  line-height: 1;
  color: rgba(119, 110, 101, 0.9);
  font-weight: 700;
  z-index: 2;
  position: absolute;
  right: 30px;
  animation: move-up 0.6s ease-in;
  animation-fill-mode: both;
}
.score.current-score {
  border-right: 1px solid #fff;
  border-radius: 0.25rem 0 0 0.25rem;
}
.score.current-score::before {
  content: "Score";
}
.score.best-score {
  border-radius: 0 0.25rem 0.25rem 0;
}
.score.best-score::before {
  content: "Best";
}

.retry {
  font-family: inherit;
  font-size: 1.125rem;
  line-height: 1;
  color: #f9f6f2;
  text-transform: uppercase;
  text-decoration: none;
  display: block;
  padding: 0.8rem 1.25rem 0.7rem;
  border: 0;
  background: #8f7a66;
  border-radius: 0.25rem;
  box-shadow: none;
  appearance: none;
}

.grid {
  position: relative;
  z-index: 1;
}
.grid__row {
  display: flex;
  margin: 0 -0.5rem 1rem;
}
.grid__row:last-child {
  margin-bottom: 0;
}
.grid__cell {
  width: calc(25% - 1rem);
  height: 0;
  padding-bottom: calc(25% - 1rem);
  margin: 0 0.5rem;
  background: rgba(119, 110, 101, 0.35);
  border-radius: 0.25rem;
}
.grid__cell:last-child {
  margin-right: 0;
}

.tile {
  font-size: 3.4375rem;
  line-height: 2.6;
  text-align: center;
  font-weight: 700;
  width: calc(25% - 1rem);
  height: 0;
  padding-bottom: calc(25% - 1rem);
  position: absolute;
  z-index: 10;
  background: #eee4da;
  border-radius: 0.25rem;
  transition: 0.4s ease-in-out;
  transition-property: top, left;
}
.tiles {
  width: calc(100% - 1rem);
  height: calc(100% - 1rem);
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
  z-index: 2;
}
.tile.tile-position-1-1 {
  left: calc(25% * 0 + 0.5rem);
  top: calc(25% * 0 + 0.5rem);
}
.tile.tile-position-1-2 {
  left: calc(25% * 0 + 0.5rem);
  top: calc(25% * 1 + 0.5rem);
}
.tile.tile-position-1-3 {
  left: calc(25% * 0 + 0.5rem);
  top: calc(25% * 2 + 0.5rem);
}
.tile.tile-position-1-4 {
  left: calc(25% * 0 + 0.5rem);
  top: calc(25% * 3 + 0.5rem);
}
.tile.tile-position-2-1 {
  left: calc(25% * 1 + 0.5rem);
  top: calc(25% * 0 + 0.5rem);
}
.tile.tile-position-2-2 {
  left: calc(25% * 1 + 0.5rem);
  top: calc(25% * 1 + 0.5rem);
}
.tile.tile-position-2-3 {
  left: calc(25% * 1 + 0.5rem);
  top: calc(25% * 2 + 0.5rem);
}
.tile.tile-position-2-4 {
  left: calc(25% * 1 + 0.5rem);
  top: calc(25% * 3 + 0.5rem);
}
.tile.tile-position-3-1 {
  left: calc(25% * 2 + 0.5rem);
  top: calc(25% * 0 + 0.5rem);
}
.tile.tile-position-3-2 {
  left: calc(25% * 2 + 0.5rem);
  top: calc(25% * 1 + 0.5rem);
}
.tile.tile-position-3-3 {
  left: calc(25% * 2 + 0.5rem);
  top: calc(25% * 2 + 0.5rem);
}
.tile.tile-position-3-4 {
  left: calc(25% * 2 + 0.5rem);
  top: calc(25% * 3 + 0.5rem);
}
.tile.tile-position-4-1 {
  left: calc(25% * 3 + 0.5rem);
  top: calc(25% * 0 + 0.5rem);
}
.tile.tile-position-4-2 {
  left: calc(25% * 3 + 0.5rem);
  top: calc(25% * 1 + 0.5rem);
}
.tile.tile-position-4-3 {
  left: calc(25% * 3 + 0.5rem);
  top: calc(25% * 2 + 0.5rem);
}
.tile.tile-position-4-4 {
  left: calc(25% * 3 + 0.5rem);
  top: calc(25% * 3 + 0.5rem);
}
