.tile-new {
  animation: appear 0.2s ease 0.4s;
  animation-fill-mode: both;
}
.tile-merged {
  z-index: 20;
  animation: pop 0.2s ease 0.4s;
  animation-fill-mode: both;
}
@media screen and (max-width: 767px) {
  .tile {
    font-size: 30px;
  }
}

.message {
  font-size: 3.75rem;
  line-height: 1;
  text-align: center;
  font-weight: 700;
  display: none;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(238, 228, 218, 0.75);
  z-index: 3;
  animation: fade-in 0.8s ease 0.4s;
  animation-fill-mode: both;
}
.message p {
  margin: 2rem;
}
.message__actions {
  display: block;
  margin-top: 2rem;
}
.message.game-won {
  color: #f9f6f2;
  background: rgba(237, 194, 46, 0.75);
}
.message.game-won, .message.game-over {
  display: flex;
}

/* prettier-ignore */
social-links {
  /* stylelint-disable-line selector-type-no-unknown */
  color: rgba(119, 110, 101, 0.85);
  display: block;
}